import {
  ComponentMeta,
} from "@plasmicapp/host";

import React from "react";
import { VimeoLite, YoutubeLite } from "@lite-embed/react";
export function ensure<T>(x: T | null | undefined): T {
  if (x === null || x === undefined) {
    debugger;
    throw new Error(`Value must not be undefined or null`);
  } else {
    return x;
  }
}

const modulePath = "@plasmicpkgs/plasmic-lite-embed";


interface LiteEmbedProps {
  service?: string,
  urlOrId?: string;
  adNetwork?: boolean;
  className?: string;

}

export const LiteEmbedMeta: ComponentMeta<LiteEmbedProps> = {
  name: "LiteEmbed",
  displayName: "Lite Embed",
  importName: "LiteEmbed",
  importPath: modulePath,
  providesData: true,
  description:
    "Embedding YouTube and Vimeo  videon on your website",

  props: {

    service: {
      type: "choice",
      options: ["Vimeo", "Youtube"],
    },
    urlOrId: {
      type: "string",

      displayName: "Video url or Id",
      description:
        "Id or url of the video which you want to embed",
    },
    adNetwork: {
      type: "boolean",
      displayName: "Ads",
      description:
        "By default the ads are disabled to respect the privacy of the users. You can enable them by setting this to true",
      defaultValue: false,
    },
  },
};

export function LiteEmbed({
  service,
  urlOrId,
  adNetwork,
  className,
}: LiteEmbedProps) {

  let video;

  if (!urlOrId) {
    return <div>Please enter URL or ID of the video</div>
  }

  if (service === 'Vimeo') {
    video = (

      <VimeoLite urlOrId={urlOrId!} adNetwork={adNetwork} ></VimeoLite>
    )
  } else {
    video = (

      <YoutubeLite urlOrId={urlOrId!} adNetwork={adNetwork} ></YoutubeLite>
    )
  }

  return (
    <div className={className}>{video}</div>
  );
}
